import React, {useEffect} from 'react'
const Logout = () => {
  useEffect(() => {
    fetch('http://localhost:5000/api/auth/logout', {
      method: "GET",
      headers: {
        "Content-Type" : "application/json"
      },
    }).then((res) => {
      console.log("wrs");
      if(res.status !== 200)
      {
        const error = new Error(res.error);
        throw error;
      }
    }).catch((err) => {
      console.log(err);
    });
  });
  return (
    <div className = "alignment">
      <h2>Logging out</h2>
      </div>
  )
}

export default Logout